#block-sf-page-title {
  h1 {
    font-size: 175%;
  }
  margin: 0 auto;
  max-width: 600px;
}


body.view-contacts-page-1,
body.view-investor-documents-page-4,
body.view-investor-documents-personal-page-1 {
  main {
    max-width: none;
    &.outer-wrapper {
      max-width: none;
    }
  }
  #block-sf-page-title {
    max-width: none;
  }
  .page-title {
    max-width: none;
  }
}

body {
  #header-background {
    position: fixed;
    width: 100vw;
    height: 100vh;
//  background-image: url(/themes/custom/sf/img/bg_one1.jpeg);
    background-size: cover;
    z-index: -1;
  }
}

footer {
  position: relative;
  height: 200px;
  text-align: center;
  //  background: $color-black;
  background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.4) 30%, rgba(0,0,0,.6) 100%);
  @media only screen and (max-width: $bp_tablet_max) {
    height: 32vh;
    //    background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.4) 30%, rgba(0,0,0,.7) 100%);
  }

  .links {
    p {
      margin: .3rem 0;
      line-height: 1.3;
      a {
        font-size: 105%;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
        @media only screen and (max-width: $bp_tablet_max) {
          font-size: 100%;
        }
      }
    }
    text-align: right;
    position: absolute;
    right: 30px;
    bottom: .5rem;
  }

  .aureon-logo {
    margin: 0 auto;
    padding: 3rem 0 0;
    @media only screen and (max-width: $bp_tablet_max) {
      padding: 2rem 0 0;
    }
  }
  .container {
    @media only screen and (max-width: $bp_tablet_max) {
      padding-top: 1vh;
    }
  }

  img {
    margin: 0 auto;
    display: block;
    &.aureon {
      height: 8vh;
    }
    &.slogan {
      height: 2.5vh;
      margin-top: .5rem;
    }
  }
}
